$primary-dark: #021727;
$primary-light: #fbceb1;
$secondary-dark: #124e78;
$secondary-light: #fffbfe;

$blue-accent: #50c5e2;
$pink-accent: #f78d94;

.app {
  height: 100%;
  display: flex;
  flex-direction: column;
}

a {
  color: $pink-accent;
}

h1 {
  font-size: 1.75rem;
}

header {
  background-color: $primary-dark;
  box-shadow: 0 0 1em $primary-dark;
  z-index: 3;
  padding-top: 8px;
  padding-right: 8px;
  position: fixed;
  width: 100%;

  nav {
    display: flex;
    justify-content: space-between;

    ul {
      display: flex;
      padding: 8px;
      list-style-type: none;

      li {
        padding-left: 16px;
      }
    }
  }
}

.home {
  height: 100%;
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex: 1 1 auto;
}

.trabalhos {
  height: 100%;
  visibility: visible;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex: 1 1 auto;
  color: $primary-dark;

  .icon {
    color: $primary-dark;
    margin: 0 8px;
  }
}

.Circle {
  width: 20px;
  height: 20px;
  border: 1px solid white;
  border-radius: 50%;
}

.info {
  padding: 8px 0;
  z-index: 2;
}

.pattern {
  position: absolute;
  z-index: 1;
  width: 75%;
  height: 75%;

  svg {
    top: 50%;
    height: 100%;
    width: 100%;

    #first {
      fill: none;
      stroke-width: 1;
    }

    #second {
      fill: none;
      stroke-width: 1;
    }

    #third {
      fill: none;
      stroke-width: 1;
      stroke: $blue-accent;
    }
  }
}

.PrimaryDark {
  background-color: $primary-dark;
  color: $secondary-light;

  .info {
    background-color: $primary-dark;
  }

  #first {
    stroke: $primary-light;
  }

  #second {
    stroke: $secondary-dark;
  }
}

.PrimaryLight {
  background-color: $primary-light;
  color: $secondary-dark;

  #first {
    stroke: $primary-dark;
  }

  #second {
    stroke: $secondary-light;
  }
}

.SecondaryLight {
  background-color: $secondary-light;
  color: $primary-dark;

  #first {
    stroke: $secondary-dark;
  }

  #second {
    stroke: $primary-light;
  }
}

.SecondaryDark {
  background-color: $secondary-dark;
  color: $secondary-light;

  #first {
    stroke: $primary-light;
  }

  #second {
    stroke: $primary-dark;
  }
}
